@import 'reset';
// @import 'mixins';
@import 'vars';
@import 'generated/sprite-svg';
@import 'animated';
@import 'libs/slick';
@import 'libs/slick-theme';
html {
  box-sizing: border-box;
  background-color: #522E91;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
img {
  max-width: 100%;
}
:root {
  font-family: $font-primary;
  line-height: normal;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}

.wrapper {
  width: 100%;
  overflow: hidden;
  font-family: $font-primary;
  color: #fff;
  font-size: 15px;
  line-height: 1.3;
  .icon {
    fill: currentColor;
  }
}

.carousel-wrapper {
  overflow: hidden;
  .slick-next {
    right: 25px;
  }
  .slick-prev {
    left: 25px;
    z-index: 1;
  }
}
a {
  color: inherit;
}
picture {
  source,
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.bg-absolute {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center bottom;
  }
}
.circle {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  overflow: hidden;
  p {
    max-width: 70%;
  }
}
img {
  border: 0;
}

img:not([src]):not([srcset]) {
  visibility: hidden;
}

.lazyload {
  animation-name: fade;
  animation-duration: 1s;
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Btn Ripple Out */

@-webkit-keyframes hvr-ripple-out {
  100% {
    top: -8px;
    right: -8px;
    bottom: -8px;
    left: -8px;
    opacity: 0;
  }
}

@keyframes hvr-ripple-out {
  100% {
    top: -8px;
    right: -8px;
    bottom: -8px;
    left: -8px;
    opacity: 0;
  }
}

.hvr-ripple-out {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
}

.hvr-ripple-out:before {
  content: '';
  position: absolute;
  border: #ed1c24 solid 4px;
  border-radius: 6.8px;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  animation-duration: 1s;
}

.hvr-ripple-out:hover:before,
.hvr-ripple-out:focus:before,
.hvr-ripple-out:active:before {
  animation-name: hvr-ripple-out;
}

/* end of Btn Ripple Out */
.icon {
  font-size: 20px;
}

// Artboard

.artboard {
  display: flex;
  position: relative;
  min-height: 100vh;
}
.artboard--person {
  .artboard__inner {
    padding: 100px 70px 50px 60px;
  }
  .technical-ref {
    font-family: 'Helvetica', $font-primary;
    font-size: 8px;
    font-weight: 300;
    &.bottom {
      margin-top: -11px;
    }
    &.right {
      position: absolute;
      right: -65px;
      bottom: 118px;  
      transform: rotate(-90deg)
    }
  }
}
.artboard__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  z-index: 1;
  max-width: 630px;
  width: 100%;
  margin: 0 auto;
}
.person__pic {
  width: 360px;
  height: 360px;
  margin: 0 auto;
}
.textblock1 {
  margin-top: 60px;
  align-self: flex-end;
  max-width: 185px;
  font-weight: 300;

  .icon-reddys {
    font-size: 39.5px;
  }
  .link {
    font-size: 12px;
  }
}
.textblock1__drreddys {
  margin: 32px 0 16px;
}
.text-bold1 {
  margin-top: 4px;
  font-weight: 900;
  font-size: 22px;
font-family: $font-heading;
line-height: 1;
}
.artboard--case {
  .artboard__inner {
    padding: 210px 55px 40px 70px;
  }
}
.textblock2 {
  .circle.xs {
    margin: 34px 0 32px -15px;
  }
}
.textblock2__capting {
  max-width: 284px;
  font-size: 36px;
  font-weight: bold;
  line-height: 1;
}
.drreddys-icon {
  margin: auto 0 0 auto;
  .icon {
    font-size: 60px;
  }
}
// end Artboard
.circle {
  &.xs {
width: 153px;
height: 153px;
  } 
  &.regular {
width: 240px;
height: 240px;
  } 
